import React, { useState, useEffect, useRef } from 'react';
import GlobalStyle from './styles/GlobalStyle';
import Header from './components/Header';
import Hero from './components/Hero';
import About from './components/About';
import Research from './components/Research';
import Professor from './components/Professor';
import Members from './components/Members';
import Gallery from './components/Gallery';
import Location from './components/Location';
import Footer from './components/Footer';
import ChatBot from './components/ChatBot';
import AnimatedSection from './components/AnimatedSection';

function App() {
  const heroRef = useRef(null); // aboutRef  heroRef 
  const [isHeroInView, setIsHeroInView] = useState(true); //  true 

  useEffect(() => {
    const handleScroll = () => {
      if (heroRef.current) {
        const { bottom } = heroRef.current.getBoundingClientRect();
        // Hero      false (Hero )
        const inView = bottom > 0;
        setIsHeroInView(inView);
      }
    };

    window.addEventListener('scroll', handleScroll);
    handleScroll(); //      

    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  useEffect(() => {
    // isHeroInView   body  
    if (isHeroInView) {
      document.body.style.backgroundImage = `url(${process.env.PUBLIC_URL + '/images/space.jpg'})`;
      document.body.style.backgroundSize = 'cover';
      document.body.style.backgroundPosition = 'center';
      document.body.style.backgroundAttachment = 'fixed';
      document.body.style.transition = 'background-image 1s ease-in-out, background-color 1s ease-in-out';
      document.body.style.backgroundColor = 'transparent'; //      
    } else {
      document.body.style.backgroundImage = 'none';
      document.body.style.backgroundColor = 'var(--background-dark1)'; //  
      document.body.style.transition = 'background-image 1s ease-in-out, background-color 1s ease-in-out';
    }
  }, [isHeroInView]);

  return (
    <>
      <GlobalStyle />
      <Header />
      <main> {/* Removed inline style */}
        <Hero ref={heroRef} /> {/* heroRef  */}
        <AnimatedSection>
          <About />
        </AnimatedSection>
        <Research />
        <Gallery />
        <Professor />
        <Members /> {/* AnimatedSection  */}
        <AnimatedSection>
          <Location />
        </AnimatedSection>
      </main>
      <Footer />
      <ChatBot />
    </>
  );
}

export default App; 