import React, { useState, useRef, useEffect } from 'react';
import styled, { keyframes, css } from 'styled-components'; // css  
import { motion, AnimatePresence } from 'framer-motion';

// --- Styled Components ---
const FloatingButton = styled.button`
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: #1a1a1a;
  border: 2px solid #00bcd4;
  cursor: pointer;
  z-index: 4000;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
  box-shadow: 0 0 15px rgba(0, 188, 212, 0.5);
  overflow: hidden;

  &::before {
    content: '';
    position: absolute;
    width: 40px;
    height: 40px;
    background: radial-gradient(circle, #00bcd4 0%, transparent 70%);
    opacity: 0.5;
    animation: pulse 2s infinite;
  }

  &:hover {
    transform: scale(1.1);
    box-shadow: 0 0 35px rgba(0, 188, 212, 1); //    
    border-color: #fff;
  }

  @keyframes pulse {
    0% {
      transform: scale(0.8);
      opacity: 0.5;
    }
    50% {
      transform: scale(1.1);
      opacity: 0.8;
    }
    100% {
      transform: scale(0.8);
      opacity: 0.5;
    }
  }

  img {
    width: 35px;
    height: 35px;
    z-index: 1;
    transition: transform 0.3s ease;
  }

  &:hover img {
    transform: rotate(180deg);
  }
`;

const neonRotate = keyframes`
  0% {
    border-color: #00bcd4; /* Cyan */
    box-shadow: 0 0 15px #00bcd4, 0 0 25px #00bcd4, inset 0 0 15px #00bcd4;
  }
  25% {
    border-color: #00aacc; /* Lighter Cyan */
    box-shadow: 0 0 20px #00aacc, 0 0 35px #00aacc, inset 0 0 20px #00aacc;
  }
  50% {
    border-color: #00bcd4; /* Cyan */
    box-shadow: 0 0 15px #00bcd4, 0 0 25px #00bcd4, inset 0 0 15px #00bcd4;
  }
  75% {
    border-color: #00aacc; /* Lighter Cyan */
    box-shadow: 0 0 20px #00aacc, 0 0 35px #00aacc, inset 0 0 20px #00aacc;
  }
  100% {
    border-color: #00bcd4; /* Cyan */
    box-shadow: 0 0 15px #00bcd4, 0 0 25px #00bcd4, inset 0 0 15px #00bcd4;
  }
`;

const ChatContainer = styled(motion.div).withConfig({
  shouldForwardProp: (prop) => !['isChatLoading'].includes(prop),
})`
  position: fixed;
  bottom: 90px;
  right: 20px;
  width: 320px;
  max-height: 500px;
  background: var(--background-dark2);
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  z-index: 4000;
  border: 2px solid transparent; /*   */
  ${({ isChatLoading }) =>
    isChatLoading &&
    css` /*  css   */
      animation: ${neonRotate} 1.5s infinite alternate; /*    */
      box-shadow: 0 0 20px rgba(0, 188, 212, 0.7); /*     */
    `}
`;

const Header = styled.div`
  background: var(--background-dark1);
  padding: 10px;
  text-align: center;
  font-weight: 600;
`;

const Messages = styled.div`
  flex: 1;
  padding: 10px;
  overflow-y: auto;
  scroll-behavior: smooth;

  /*   */
  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background: var(--background-dark1);
  }

  &::-webkit-scrollbar-thumb {
    background: #444;
    border-radius: 4px;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
`;

const InputWrapper = styled.form`
  display: flex;
  border-top: 1px solid var(--background-dark1);
`;

const TextInput = styled.input`
  flex: 1;
  background: transparent;
  border: none;
  padding: 10px;
  color: var(--text-color);
  &:focus { outline: none; }
`;

const SendButton = styled.button`
  background: var(--primary-color);
  border: none;
  padding: 0 15px;
  cursor: pointer;
`;

const Bubble = styled.div`
  background: ${({ role }) => (role === 'user' ? '#2e2e2e' : '#444')};
  color: var(--text-color);
  padding: 8px 12px;
  border-radius: 8px;
  margin-bottom: 8px;
  align-self: ${({ role }) => (role === 'user' ? 'flex-end' : 'flex-start')};
  max-width: 80%;
  white-space: pre-line;
`;

const API_ENDPOINT = 'http://localhost:5000/chat'; //    URL 

const personData = {
  '': {
    role: '',
    details: 'Optics, Automotive AI   . GPT  , Phocus Camera/Vision    . ',
  },
  '': {
    role: '',
    details: 'FEM, Defense Industry   . CAE     . ',
  },
  '': {
    role: '',
    details: ' , AI     . ',
  },
};

function ChatBot() {
  const [open, setOpen] = useState(false);
  const [messages, setMessages] = useState([
    { role: 'assistant', content: ', .  J.A.R.V.I.S..  ? ' } // J.A.R.V.I.S.  
  ]);
  const [input, setInput] = useState('');
  const [loading, setLoading] = useState(false);
  const messagesEndRef = useRef(null);
  const [isChatLoading, setIsChatLoading] = useState(false); //    

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: "smooth" });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]); //    

  const handleSend = async (e) => {
    e.preventDefault();
    const content = input.trim();
    if (!content) return;
    const newMessages = [...messages, { role: 'user', content }];
    setMessages(newMessages);
    setInput('');
    setLoading(true);
    setIsChatLoading(true); //       

    let assistantMsg = '';

    //    
    const nameQuery = Object.keys(personData).find(name => content.includes(name));
    if (nameQuery) {
      const person = personData[nameQuery];
      assistantMsg = `${nameQuery} ${person.role}.    ${person.details} ? `;
      setMessages((prev) => [...prev, { role: 'assistant', content: assistantMsg }]);
      setLoading(false);
      setIsChatLoading(false); //         
      return;
    }

    //   
    try {
      const response = await fetch(API_ENDPOINT, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json; charset=UTF-8', // UTF-8 charset 
        },
        body: JSON.stringify({ message: content }),
      });

      if (!response.ok) {
        const errorData = await response.json();
        console.error("Backend error response:", errorData);
        throw new Error(`HTTP ${response.status}: ${errorData.error || '   '}. ${errorData.details || ''}`);
      }

      const data = await response.json();
      let rawAssistantMsg = data.response || '  . ';
      
      // 'sir'     
      rawAssistantMsg = rawAssistantMsg.replace(/sir|Sir/g, '');

      //     
      if (rawAssistantMsg.includes('') || rawAssistantMsg.includes('')) {
        assistantMsg = rawAssistantMsg + ' ';
      } else if (rawAssistantMsg.includes('') || rawAssistantMsg.includes('')) {
        assistantMsg = rawAssistantMsg + ' ';
      } else if (rawAssistantMsg.includes('') || rawAssistantMsg.includes('')) {
        assistantMsg = rawAssistantMsg + ' ';
      } else {
        assistantMsg = rawAssistantMsg + ' ';
      }

      setMessages((prev) => [...prev, { role: 'assistant', content: assistantMsg }]);
    } catch (err) {
      console.error('Error:', err);
      let errorMessage = ' .    . ';
      if (err.message.includes('  DK-VAULT DDOS   .')) {
        errorMessage = '  DK-VAULT DDOS   .    .';
      }
      setMessages((prev) => [...prev, { role: 'assistant', content: errorMessage }]);
    } finally {
      setLoading(false);
      setIsChatLoading(false); //         
    }
  };

  const containerVariants = {
    hidden: { opacity: 0, y: 100, scale: 0.7 }, //     
    visible: { opacity: 1, y: 0, scale: 1, transition: { type: "spring", damping: 15, stiffness: 120 } }, //  
    exit: { opacity: 0, y: 100, scale: 0.7, transition: { duration: 0.3 } },
  };

  return (
    <>
      <FloatingButton onClick={() => setOpen((o) => !o)}>
        <img src={process.env.PUBLIC_URL + '/images/arc-reactor.svg'} alt="JARVIS" />
      </FloatingButton>
      <AnimatePresence>
        {open && (
          <ChatContainer
            variants={containerVariants}
            initial="hidden"
            animate="visible"
            exit="exit"
            isChatLoading={isChatLoading} // isChatLoading prop 
          >
            <Header> AI  </Header>
            <Messages>
              {messages.map((m, idx) => (
                <Bubble key={idx} role={m.role}>{m.content}</Bubble>
              ))}
              {loading && <Bubble role="assistant">  ...</Bubble>}
              <div ref={messagesEndRef} />
            </Messages>
            <InputWrapper onSubmit={handleSend}>
              <TextInput
                value={input}
                onChange={(e) => setInput(e.target.value)}
                placeholder=" "
                disabled={isChatLoading} //    
              />
              <SendButton type="submit"></SendButton>
            </InputWrapper>
          </ChatContainer>
        )}
      </AnimatePresence>
    </>
  );
}

export default ChatBot; 