import React, { forwardRef } from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import Particles from '@tsparticles/react';
import { loadSlim } from '@tsparticles/slim';

const HeroSection = styled.section`
  height: 100vh;
  /* background:
    linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url(${process.env.PUBLIC_URL + '/images/space.jpg'});
  background-size: cover;
  background-position: center;
  background-attachment: fixed; */
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative; // Particles  position

  @media (max-width: 768px) {
    background-color: var(--background-dark1); //  HeroSection    
  }
`;

const ContentContainer = styled.div`
  /* GlobalStyle .container    */
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 40px;
  
  /*   Flexbox  */
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  @media (max-width: 768px) {
    padding: 0 20px; //   
  }
`;

const HeroContent = styled.div`
  /* HeroContent      */
  flex-shrink: 0; 
  max-width: 800px;
  text-align: center;
  
  h5 {
    font-size: 1.2rem;
    font-weight: 500;
    letter-spacing: 4px;
    color: var(--text-color-muted);
    font-family: var(--font-orbitron); // Orbitron  

    @media (max-width: 768px) {
      font-size: 1rem; //    
      letter-spacing: 2px; //   
    }
  }

  h1 {
    font-family: var(--font-orbitron); // Orbitron  
    font-size: clamp(3rem, 10vw, 5.5rem);
    font-weight: 400; /*     ( ) */
    letter-spacing: 8px; /*      */
    white-space: nowrap; /*   */
    line-height: 1.1;
    margin: 20px 0;
    color: var(--primary-color);
    text-shadow: 0 4px 15px rgba(0, 0, 0, 0.5);

    @media (max-width: 768px) {
      font-size: clamp(2rem, 8vw, 4rem); //    
      letter-spacing: 4px; //   
      white-space: normal; //   
    }
  }

  p {
    font-size: 1.2rem;
    max-width: 600px;
    margin: 0 auto 30px;
    color: var(--text-color);
    font-family: var(--font-orbitron); // Orbitron  

    @media (max-width: 768px) {
      font-size: 1rem; //    
      margin-bottom: 20px; //   
    }
  }
`;

const Spacer = styled.div`
  /* HeroContent ScrollIndicator    */
  flex-grow: 1;
`;

const ScrollIndicator = styled(motion.div)` // motion.div 
  /* position: absolute  */
  flex-shrink: 0; /*    */
  padding-bottom: 30px; /*   */

  color: var(--primary-color);
  font-size: 0.9rem;
  letter-spacing: 1px;
  opacity: 0.7;

  span {
    display: block;
    margin-top: 10px;
    width: 2px;
    height: 30px;
    background-color: var(--primary-color);
    margin-left: auto;
    margin-right: auto;
  }

  /* @keyframes scrollDown {
    0% { transform: translateY(-10px); opacity: 0; }
    50% { opacity: 1; }
    100% { transform: translateY(10px); opacity: 0; }
  } */
`;

// --- Animation Variants --- 
const containerVariants = {
  hidden: { opacity: 0 },
  visible: { 
    opacity: 1, 
    transition: {
      staggerChildren: 0.2, //   0.2  
      delayChildren: 0.5 //     0.5    
    }
  }
};

const itemVariants = {
  hidden: { y: 20, opacity: 0 },
  visible: { 
    y: 0, 
    opacity: 1,
    transition: {
      type: "spring",
      damping: 10,
      stiffness: 100
    }
  }
};

function Hero(_, ref) {
  const particlesInit = async (main) => {
    await loadSlim(main);
  };

  const isMobile = window.innerWidth <= 768; //   

  const particlesOptions = {
    background: {
      color: {
        value: "#000000", //  
      },
    },
    fpsLimit: 60,
    interactivity: {
      events: {
        onClick: {
          enable: false,
          mode: "push",
        },
        onHover: {
          enable: true,
          mode: "repulse",
        },
        resize: true,
      },
      modes: {
        push: {
          quantity: 4,
        },
        repulse: {
          distance: 100,
          duration: 0.4,
        },
      },
    },
    particles: {
      color: {
        value: "#ffffff", //  
      },
      links: {
        color: "#ffffff",
        distance: 150,
        enable: false, //  
        opacity: 0.5,
        width: 1,
      },
      collisions: {
        enable: false,
      },
      move: {
        direction: "none",
        enable: true,
        outModes: {
          default: "bounce",
        },
        random: true,
        speed: isMobile ? 0.1 : 0.3, //     
        straight: false,
      },
      number: {
        density: {
          enable: true,
          area: 800,
        },
        value: isMobile ? 40 : 80, //    
      },
      opacity: {
        value: 0.5, //  
      },
      shape: {
        type: "circle",
      },
      size: {
        value: isMobile ? { min: 0.5, max: 2 } : { min: 1, max: 3 }, //    
      },
    },
    detectRetina: true,
  };

  return (
    <HeroSection id="home" ref={ref}>
      {!isMobile && ( //    Particles 
        <Particles
          id="tsparticles"
          init={particlesInit}
          options={particlesOptions}
          style={{
            position: "absolute",
            top: 0,
            left: 0,
            width: "100%",
            height: "100%",
            zIndex: -1, //  
          }}
        />
      )}
      <ContentContainer>
        <Spacer /> {/*     */}
        <HeroContent
          as={motion.div}
          variants={containerVariants}
          initial="hidden"
          animate="visible"
        >
          <motion.h5 variants={itemVariants}>Dongyeun Lee</motion.h5>
          <motion.h1 variants={itemVariants}>Robot system lab</motion.h1>
          <motion.p variants={itemVariants}>FEM  ,   </motion.p>
        </HeroContent>
        <Spacer /> {/*     */}
        <ScrollIndicator
          as={motion.div}
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 0.7, y: 0 }}
          transition={{ delay: 2.5, duration: 1, repeat: Infinity, repeatType: "reverse" }}
        >
          SCROLL
          <span />
        </ScrollIndicator>
      </ContentContainer>
    </HeroSection>
  );
}

export default forwardRef(Hero); 