import React, { useState } from 'react';
import styled from 'styled-components';
import { motion } from 'framer-motion';
import GalleryModal from './GalleryModal';

const GallerySection = styled.section`
  background: var(--background-dark1); /*    */
  color: var(--text-color);
  padding: 100px 0; /*    */

  @media (max-width: 768px) {
    padding: 60px 0; //   
  }
`;

const SectionTitle = styled.h2`
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 60px;

  @media (max-width: 768px) {
    font-size: 2rem; //    
    margin-bottom: 40px;
  }
`;

const Grid = styled(motion.div)`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 30px;

  @media (max-width: 768px) {
    grid-template-columns: 1fr; //   
    gap: 20px; //   
  }
`;

const ImageWrapper = styled(motion.div)`
  overflow: hidden;
  border-radius: 8px;
  height: 200px; //    (  )
  
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
  }
  &:hover img {
    transform: scale(1.1);
  }

  @media (max-width: 768px) {
    height: 180px; //    
  }
`;

// --- Animation Variants --- 
const containerVariants = {
  hidden: { opacity: 0 },
  visible: {
    opacity: 1,
    transition: {
      staggerChildren: 0.1, //   0.1  
      delayChildren: 0.2, //     0.2    
    },
  },
};

const itemVariants = {
  hidden: { y: 20, opacity: 0 },
  visible: {
    y: 0,
    opacity: 1,
    transition: {
      duration: 0.5,
      ease: 'easeOut',
    },
  },
};

const items = [
  {
    src: process.env.PUBLIC_URL + '/images/image1.jpg',
    title: 'Pure Rotation Scanner',
    description: `    \n\n           .\n\n  :  312Hz\n   : 497.2 rad\n\nAFM( ),      \n\n    ,    .`
  },
  {
    src: process.env.PUBLIC_URL + '/images/image2.jpg',
    title: 'Active Vibration Isolator',
    description: `   \n\n Gimac   \n Matlab    \n Recurdyn   \n Massed    \n\n         .`
  },
  {
    src: process.env.PUBLIC_URL + '/images/image3.jpg',
    title: 'Passive Vibration Isolator',
    description: `   \n\n       ,         .\n\n  (Hydraulic Damper) \n     \n Matlab, Recurdyn         \n\n     .`
  },
  {
    src: process.env.PUBLIC_URL + '/images/image4.jpg',
    title: 'Adjustable Planar Flexure',
    description: `    ( )\n\n           .\n\n m      \n     \n    FEM     3%    \n\n  ,    .`
  },
];

function Gallery() {
  const [selected, setSelected] = useState(null);

  return (
    <GallerySection id="gallery">
      <div className="container">
        <SectionTitle
          as={motion.h2}
          initial={{ opacity: 0, y: -50 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, amount: 0.5 }}
          transition={{ duration: 0.8, ease: "easeOut" }}
        >
          GALLERY
        </SectionTitle>
        <Grid
          variants={containerVariants}
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, amount: 0.1 }}
        >
          {items.map((item, idx) => (
            <ImageWrapper
              key={idx}
              variants={itemVariants}
              whileHover={{ scale: 1.02 }}
              onClick={() => setSelected(item)}
              style={{ cursor: 'pointer' }}
            >
              <img src={item.src} alt={item.title} />
            </ImageWrapper>
          ))}
        </Grid>
      </div>
      <GalleryModal item={selected} onClose={() => setSelected(null)} />
    </GallerySection>
  );
}

export default Gallery; 